import React, { useEffect } from 'react';
import nprogress from 'nprogress';

nprogress.configure({
  easing: 'ease', // 
  speed: 500, // 
  showSpinner: false, // ico
  trickleSpeed: 200, // 
  minimum: 0.3 // 
});

const Loading: React.FC = () => {
  useEffect(() => {
    nprogress.start();
  }, []);
  useEffect(() => () => {
    nprogress.done();
  });
  // eslint-disable-next-line react/jsx-no-useless-fragment
  return <></>;
};

export default Loading;
